<template>
	<div class="_full_router fund-manage-con">
			<custHeader :title="title"></custHeader>
			<div class="fund-manage-contain form-submit">
				<div v-show="flowId==1">
				<!-- <mt-field label="证件类型" placeholder="请选择证件类型" v-model="idType.name" v-lfoucs="selectTypeVisibleShow" v-remoteoptions="dicts.idType"></mt-field>
				<mt-field label="证件号码" placeholder="请输入证件号码" v-model="idCode" ></mt-field> -->
                <mt-field label="手机号码" placeholder="请输入手机号码" type="tel" v-validtype="rules.mb" :state="rules.mb.state" v-model="mb"></mt-field>
                <mt-field label="验证码" placeholder="请输入验证码" v-model="captcha" v-validtype="rules.captcha" :state="rules.captcha.state"><mt-button size="small" type="primary" :disabled=idDisabled @click="send">{{ capMessage }}</mt-button></mt-field>
				<mt-button  type="primary" size="large"  @click="checkSmsCode" :disabled="valiSuccess">下一步</mt-button>
				</div>
        </div>
	</div>
</template>
<script>
import {Field,Picker,Popup,MessageBox,Actionsheet,Toast} from 'mint-ui'
import custHeader from  'components/custHeader'
import zjz from  'assets/2276.png'

Vue.component(Actionsheet.name,Actionsheet);
Vue.component(Field.name,Field)
Vue.component(Popup.name,Popup)
Vue.component(Picker.name,Picker)
export default{
	data(){
		return {
			picMsg:"",

			dicts:{
                idType:{
                    name:'ID_TYPE',
                    dataKey:"selectTypeSlots"
                }
		},
			actions:[{name:"拍照",
						method:()=>{
							this.selectPic();
						}
						},
						{name:"从相册选择",
						method:()=>{
							this.selectPic();
						}
						}
			],
			sheetVisible:false,
			zjzshow:false,
			zjz:zjz,
		flowId:"1",
		title:"找回密码",
        capMessage: '发送短信',
		idType:{name:"",value:""},
		idName:"",
		idCode:"",
		certValidDate:"",
		idIssAgcy:"",

		nationality:{name:"",value:""},
		province:{name:"",value:""},
		city:{name:"",value:""},
		area:{name:"",value:""},
		postcode:"",



		bankCode:"",
		bankbranch:"",
		bankNet:"",
		bankAcct:"",


    mb:'',
    captcha:'',
    password:'',
    rules:{
      mb:{
        str:"required,mobile",
        state:'',
        message:'',
      },
      captcha:{
        str:"required",
        state:''
      }
    }

		}
	},
	created:function(){
	//	this.qryCustBaseInfo();
	},
	 components:{
    custHeader
  },
    computed:{
    valiSuccess:function(){
      return (this.mb!="" && this.captcha!="")? false:true;
    }
  },
	methods:{
        setPassword:function(){
          
            if(global.isOpenMd5){
              this.password=global.md5(this.password);
            }
            var pars={
                password:this.password,
                useScope:global.USE_SCOPE.LOGINPASSWORD,// 0登录/交易1资金业务
                authType: global.AUTH_TYPE,
                mobilePhone:this.mb
            }
            axiosUtil.upCltPasswordBack(pars).then(data=>{
                var data=data.data;
                if(data.errorcode==0){
                    	MessageBox("提示","重设成功")
                }else{
                    	MessageBox("提示",data.errormsg)
                }
            })
        },
		 qryCustBaseInfo:function(){
        axiosUtil.qryCustBaseInfo().then(data=>{
          var data=data.data;
          console.log(data);
        // data= JSON.parse(data.data)
        this.idType.value=data.data.idType;
        if(data.errorcode==0){
        	window.dict("ACCT_TYPE",data.data.idType).then(data=>{
        		this.idType.name=data

        	});
        }else{
        	MessageBox("提示",data.errormsg)
        }

        })
      },
		upload:function(e){
			let files=e.target.files || e.dataTransfer.files;
			if(!files.length ) return;
			var pars=new FormData();
			pars.append("file",files[0],files[0].name);
			pars.append("uploadUser","279947");
			pars.append("docBizId","4001");
			pars.append("docBizSubid","1");
			axiosUtil.realNamePicUpload(pars).then(data=>{
			})


		},
		showSheet:function(){
			this.sheetVisible=true;
		},
		selectPic:function(){
			this.zjzshow=true;

		},
		selectTypeChange:function(picker,value){
			console.log(value);
			if(value[0]){
				this.idType.name=value[0].name;
                this.idType.value=value[0].value;
			}
		},

		dissSelectType:function(){
			this.currency='';
			this.selectTypeVisible=false;
		},
		sureSelectType:function(){
			if(!this.currency){
		        this.currency = this.selectTypeSlots[0].values[0].name;
		      }
		      this.selectTypeVisible = false
		},
		showFlow:function(index){
				this.flowId=index;
		},
        checkSmsCode:function(){
        	if(this.rules.mb.state=="error"){
             Toast({
              message: this.rules.mb.message,
              position: "bottom",
              duration: 1000
            });
            return 
          }

            var pars={
                smsAuthCode:this.captcha,
                askBizCode:100223,
                mb:this.mb,
            }
            axiosUtil.checkSmsCode(pars).then(data=>{
                var data=data.data;
                if(data.errorcode==0){

                  //  this.flowId=2;
                  this.$router.push({path:"/findPasswordSure",query:{mb:this.mb}});
                }else{
                    MessageBox("提示", data.errormsg);
                }
            })

        },
        selectTypeVisibleShow:function(){
            this.selectTypeVisible=true;
        },
        send:function(){
          var that = this;
          if(!(this.rules.mb.state !== 'error' &&  this.rules.mb.state !== '' && this.mb)){
            MessageBox("提示", "你的手机号格式不正确");
            return false
          }
          axiosUtil.sendSms({
            askBizCode: 100223,//注册验证吗 和找回密码验证码
            mb:this.mb
          }).then(function(data){
            if (data.errorcode == 1) {
              MessageBox("提示", data.errormsg);
            } else{
              let time = 60;
              that.capMessage = time + "s后重新发送"
              that.idDisabled = true;
              let timer = setInterval(function(){
                time = time - 1;
                that.capMessage = time + "s后重新发送";
                if(!time){
                  // time = 60
                  that.capMessage = '发送短信'
                  that.idDisabled = false;
                  clearInterval(timer)
                }
              },1000)
            }
          })

        }
	}
}

</script>
<style scoped>
.fund-manage-con{

}
.fund-manage-contain{
	padding: 12px;
	overflow-y:auto;
	    height: 100%;
    padding-bottom: 48px;
}
.select-dict{
	font-size: 14px;
}
	.select-dict button{
		    width: 50%;
    float: left;
    height: 28px;
    border: none;
    background: #fff;
    border-top: 1px solid #ccc;
	}
	.select-dict button:last-child{
		border-left: 1px solid #ccc;
	}
	.select-dict-btns{
		position: absolute;
		bottom: 0;
		bottom: -28px;
    width: 100%;
	}
	.zjz{
		width: 100%;
	}
</style>



